.About {
    flex-direction: row;
    padding: 80px 20px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1200px;
    border: 2px solid black;
    background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
}

.ColLeft {
    width: 200px;
    height: 550px;
    flex-basis: 50%;
}


.image {
    height: 100%;
    width: 100%;
    font-size: 2rem;
    border: 8px solid crimson;
    position: relative;
    object-fit: cover;
}


.ColRight {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 50%;
    margin-left: 50px;
    color: white;
}

.ColRight h1 {
    font-size: 4rem;
    font-weight: 400;
    text-transform: capitalize;
    text-align: center;
    letter-spacing: 0.5rem;
}

.ColRight h2 {
    font-size: 1.5rem;
    font-weight: 500;
}

.ColRight a {
    border: 2px solid crimson;
    text-align: center;
    text-decoration: none;
    font-size: 1.5rem;
    margin-top: 10px;
    padding: 20px;
    color: crimson;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.ColRight span {
    color: crimson;
}

@media only screen and (max-width: 600px) {
    .About {
        flex-direction: row;
        padding: 80px 20px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column-reverse;
        max-width: 1200px;
        border: 2px solid black;
    }

    .ColRight {
        margin-left: 20px;
    }
}

